.sub-title{
  position: relative;
}
.sub-title:after{
  content: '';
	position: absolute;
	top: 100%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	width: 0%;
	height: auto;
	border-bottom: 3px solid #3e4555;
	transition: all 0.3s linear;
}
.sub-title:hover:after{
  width: 20%;
}

