.work-list{
  counter-reset: li;
  list-style: none;
  *list-style: decimal;
  font-size: 15px;
  font-family: 'Raleway', sans-serif;
  padding: 0;
  margin-bottom: 4em;
}
.work-list li{
  position: relative;
  display: block;
  padding: .4em .4em .4em 2em;
  *padding: .4em;
  margin: .5em 0;
  background: #93C775;
  color: rgb(7, 0, 0);
  text-decoration: none;
  border-radius: 10em;
  transition: all .2s ease-in-out;
}
.work-list li:hover{
  background: #d6d4d4;
text-decoration:none;
-webkit-transform: scale(1.1);
        transform: scale(1.1);
color: #000;
}
.work-list li:before{
  content: counter(li);
counter-increment: li;
position: absolute;
left: -1.3em;
top: 50%;
margin-top: -1.3em;
background:#93C775;
height: 2.5em;
width: 2.5em;
line-height: 2em;
border: .3em solid #fff;
text-align: center;
font-weight: bold;
border-radius: 2em;
color:#FFF;
}

